<template>
  <nav class="navbar navbar-expand-lg fixed-top navbar-light bg-light">
    <div class="d-flex justify-content-right ml-auto">
      <h3 class="" v-if="isLogged">{{ userName }}  您好</h3>
      <h3 class="" v-else>Guest  您好</h3>
      <button class="btn btn-warning mx-3" @click.prevent.stop="logOutSubmit" v-if="isLogged">Log Out!</button>
    </div>
  </nav>
</template>

<script>
  export default {
    data() {
      return {
        userName: '',
        isLogged: false
      }
    },
    created() {
      this.userName = localStorage.getItem('username')
      // if (this.userName) {
      //   this.isLogged = !this.isLogged
      // }
      this.$bus.$on('logged', () => { this.isLogged = !this.isLogged })
      console.log(this.isLogged)
    },
    methods: {
      logOutSubmit() {
        localStorage.removeItem('token')
        localStorage.removeItem('username')
        this.isLogged = !this.isLogged
        console.log(this.isLogged)
        this.$router.push('/signin')
      }
    }
  }
</script>
